import React from "react";
import "./About.css";

const About = () => {
  return (
    <div className="about-section">
      <h2> About Me</h2>
      <p>
        I'm Sakshi  the kind of developer who vibes with dark mode, 
        dreams in React components, and probably has 37 Chrome tabs open right now. 
        I love mixing tech + creativity  whether it's building AI tools, 
        debugging at 2AM, or just vibing to a good lo-fi playlist while coding .
      </p>
      <p>
        Back in class 11, I chose IP (Informatics Practices) as a subject  and guess what? I was the **only girl** in that class.  It felt weird at first, but slowly, that weirdness turned into confidence.
      
        I didnt grow up dreaming about writing code, but somewhere between random HTML tags and Python if-else loops  I found something I genuinely enjoyed.
      </p>

      <p>
        No, I didnt clear JEE. And honestly? That used to break me. But what no one tells you is  **growth doesnt always look glamorous**. Sometimes its messy, inconsistent, and filled with bugs (both in code and life ).
      </p>

      <p>
        Today, Im still figuring things out. Still learning. Still evolving. And Im okay with being a beautiful work-in-progress. 
      </p>

      <p className="note">
        I'm not perfect ,but i always try working to grow and make changes
      </p>
    </div>
  );
};

export default About;



